<template>
  <div id="detail">
    <div class="content" v-if="list.detail">
      <div class="header clear">
        <h2>
          <van-image :src="list.detail.auth_icon" round>
            <template v-slot:loading>
              <van-loading type="spinner" size="20"/>
            </template>
          </van-image>
        </h2>
        <p v-html="list.detail.auth"></p></div>
      <div class="cont">
        <h3 v-html="list.des"></h3>
        <div class="time"><p>{{ Time }}<span><img src="../assets/img/zan.png" alt=""/></span></p></div>
        <div class="text-box" v-html="list.detail.content"></div>
      </div>
    </div>

    <van-tabbar>
      <van-tabbar-item>
        <van-icon
            name="fanhui"
            class="iconfont"
            class-prefix="icon"
            color="#f04142"
            size="0.2rem"
            @click="onClickLeft"
        />
      </van-tabbar-item>
      <van-tabbar-item>
        <input type="text" id="pinglun" placeholder="发一条友善的评论">
      </van-tabbar-item>
      <van-tabbar-item>
        <van-icon
            name="dianzan"
            class="iconfont"
            class-prefix="icon"
            color="#f04142"
            size="0.25rem"

        />
      </van-tabbar-item>
      <van-tabbar-item>
        <van-icon
            name="guanzhu"
            class="iconfont"
            class-prefix="icon"
            color="#f04142"
            size="0.25rem"
        />
      </van-tabbar-item>
      <van-tabbar-item>
        <van-icon
            name="fenxiang"
            class="iconfont"
            class-prefix="icon"
            color="#f04142"
            size="0.25rem"
            @click="showShare = true"
        />
      </van-tabbar-item>
    </van-tabbar>
    <van-share-sheet
        v-model="showShare"
        title="立即分享给好友"
        :options="options"
        @select="onSelect"
    />
  </div>

</template>

<script>
import {mapActions, mapState, mapGetters} from 'vuex'
import {Toast} from 'vant'

export default {
  name: "detail",
  data() {
    return {
      showShare: false,
      options: [
        {name: '微信', icon: 'wechat'},
        {name: '微博', icon: 'weibo'},
        {name: '复制链接', icon: 'link'},
        {name: '分享海报', icon: 'poster'},
        {name: '二维码', icon: 'qrcode'},
      ],
    }
  },
  created() {
    this.detail({path: this.$route.query.toPath, id: this.$route.params.id})
  },
  methods: {
    ...mapActions('detail', ['detail']),

    onClickLeft() {
      this.$router.go(-1)
    },
    onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },
  },
  computed: {
    ...mapState('detail', {
      list: state => state.list
    }),
    ...mapGetters('detail', ['Time'])
  }
}
</script>

<style scoped>

html, body {
  overflow-x: hidden;
}

.content {
  max-width: 6.4rem;
  margin: 0 auto;
  margin-top: 0.15rem;
  /*background: #f2f4f5;*/
  padding-bottom: 0.53rem;
}

.content .header {
  padding: 0.23rem 0.16rem 0.08rem;
  height: 0.86rem;
  background: none
}

.header h2 {
  float: left;
  margin-right: 0.18rem;
  width: 0.5rem;
  height: 0.5rem
}

.header h2 img {
  width: 100%;
  height: 100%
}

.header p {
  float: left;
  margin-top: 0.18rem;
  font-size: 18px;
}

.content .cont {
  padding: 0 0.22rem;
  color: #494d4d;
}

.cont h3 {
  font-size: 23px;
  line-height: 0.35rem;
  padding-bottom: 0.14rem;
  border-bottom: 1px solid #494d4d;
}

.cont .time {
  height: 0.14rem;
  margin: 0.15rem 0;
}

.time p {
  float: left;
  position: relative;
}

.time span {
  width: 0.19rem;
  height: 0.19rem;
  position: absolute;
  top: -0.02rem;
  right: -0.35rem;
}

.time span img {
  width: 100%;
  height: 100%;
}

.cont .text-box {
  font-size: 15px;
}

.text-box {
  font-size:16px;
  line-height: 26px;
  text-indent:2em;
}

#pinglun{
  height: 0.28rem;
  border:1px solid #848484;
  border-radius:0.14rem;
  padding-left:20px;
}
#pinglun::placeholder{
  color: #d2d1d1
}
</style>